'use strict'

// import area
import React         from 'react'
import { withTheme } from '@material-ui/core/styles'

import Typography    from '@material-ui/core/Typography'
import Icon          from '@material-ui/core/Icon'
import Button        from '@material-ui/core/Button'
import IconButton    from '@material-ui/core/IconButton'
import Popover       from '@material-ui/core/Popover'

import VolumeUp      from '@material-ui/icons/VolumeUp'

import Slider        from 'rc-slider'
import 'rc-slider/assets/index.css';
// variables area

class VolumeControlView extends React.Component {
    constructor(props) {
        super(props)

        this.className = this.constructor.name
        this.hideTimer = undefined
        this.hideDelay = 1500
        this.state     = {
            showSlider: false
        }

        this.style = {
            root: {
                // display: 'flex'
            },
            icon: {
                fontSize: 48
            },
            sliderMenu: {
                height: '20vh',

                marginLeft: '4%',
                // marginRight: '4%',
                marginTop: '2%',
                marginBottom: '5%'
            },
            railStyle: {
                // marginLeft: '2%'
            },
            sliderText: {
                marginTop: '20%'
            },
            sliderBox: {
                display        : 'flex',
                flexDirection  : 'column',
                justifyContent : 'center',
                marginLeft     : '50%',
                width          : 48,
            }
        }

        this.buttonHandler  = this.buttonHandler.bind(this)
        this.handleClose    = this.handleClose.bind(this)
        this.onFocusOut     = this.onFocusOut.bind(this)
        this.onSliderChange = this.onSliderChange.bind(this)

        this.startHideTimer   = this.startHideTimer.bind(this)
        this.hideFunction     = this.hideFunction.bind(this)
        this.restartHideTimer = this.restartHideTimer.bind(this)
    }

    /*
    *   create the controls and subscribe to the values
    */
    componentWillMount() {

    }

    /*
    *   remove the listeners here
    */
    componentWillUnmount() {

    }

    // IDEA: use the 'component' attribute to parse the buttons as 'span'
    // than you can align them with the baseline option
    render() {
        let showSlider  = this.state.showSlider
        let playerState = this.props.musicControlStore.get('playerState')

        let currentVolume = playerState.volume


        return (
            <div id='volumeControlView' style={this.style.root}>

                {
                    !showSlider &&
                    <IconButton
                        id            = 'volumeControl'
                        onClick       = { this.buttonHandler }
                        aria-label    = 'volumeControl'
                    >
                        <VolumeUp style={this.style.icon}  />
                    </IconButton>
                }

                {
                    showSlider &&
                    <div id='sliderBox' style={this.style.sliderBox}>
                        <Slider
                            vertical     = { true }
                            min          = { 0 }
                            max          = { 100 }
                            defaultValue = { currentVolume }
                            tabIndex     = { 1 }
                            onBlur       = { this.onFocusOut }
                            onChange     = { this.onSliderChange }
                            handleStyle  = { this.style.railStyle}
                            style        = { this.style.sliderMenu }
                        />

                        <Typography type='display1' style={this.style.sliderText}>
                            { currentVolume }
                        </Typography>
                    </div>
                }
            </div>
        )
    }

    buttonHandler(event) {
        this.setState({ showSlider: true }, () => {
            this.startHideTimer()
        })
    }

    // NOTE: currently not used
    handleClose(event) {
        console.log(`[${this.className}] handleClose '${event.currentTarget.id}' called`)

        this.setState({ showSlider: false })
    }

    // NOTE: currently not used
    // IDEA: hide the slider when the focus is on an other element
    onFocusOut(event) {
        console.log('focus lost')
        console.log(event)
    }

    onSliderChange(event) {
        console.log(`[${this.className}] slider value changed to ${event}`)
        console.log(this.props)

        this.restartHideTimer()
        this.props.onKeyPressed('volume', event)
    }

    startHideTimer() {
        this.hideTimer = setTimeout(this.hideFunction, this.hideDelay)
    }

    hideFunction() {
        this.setState({ showSlider: false })
    }

    restartHideTimer() {
        clearTimeout(this.hideTimer)
        this.startHideTimer()
    }

    /**********************************************
    *
    *                  help functions
    *
    **********************************************/


}

export default withTheme()(VolumeControlView)



