'use strict'

// import area
import React               from 'react'
import { Container }       from 'flux/utils'

// flux stuff
import AppStore            from './../data/stores/appStore'
import AppActions          from './../data/actions/appActions'

import MusicControlStore   from './../data/stores/musicControlStore'
import MusicControlActions from './../data/actions/musicControlActions'

import Utils               from './../data/utils/utils'
import WebsocketEvents     from './../data/utils/websocket/websocketEvents'

// views/containers
import VolumeControlView   from './../views/controls/volumeControlView'
import SkipPreviousView    from './../views/controls/skipPreviousView'
import PlayPauseView       from './../views/controls/playPauseView'
import SkipNextView        from './../views/controls/skipNextView'
import ToggleScreenView    from './../views/controls/toggleScreenView'

// variables area

class MusicControlContainer extends React.Component {
    constructor() {
        super()

        this.className    = this.constructor.name
        this.state        = {}
        this.style        = {
            root: {
                // flexGrow       : 1,
                marginBottom   : '2%',
                // alignSelf      : 'flex-end',
                alignItems     : 'flex-end',
                justifyContent : 'space-around'
            }
        }
    }

    /*
    *
    */
    componentDidMount() {
        // register this application at the server
        let registration = {
            type: WebsocketEvents.ROLE_USER
        }

        // AppActions.registerAtServer(registration)
    }

    /*
    *   loads all necessary stores
    *   NOTE: this is a required function for a container
    */
    static getStores() {
        let stores = [
            AppStore,
            MusicControlStore
        ]

        return stores
    }

    /*
    *   load your state here
    *   NOTE: this is a required function for a container
    */
    static calculateState(prevState) {
        let state = {
            appStore          : AppStore.getState(),
            musicControlStore : MusicControlStore.getState(),

            onKeyPressed      : MusicControlActions.keyPressed
        }

        return state
    }

    render() {
        let appStore = this.state.appStore
        let theme    = appStore.get('theme').object

        return (
            <div id='musicContent' className='flexContainer' style={this.style.root}>
                <VolumeControlView {...this.state} />

                <div id='centeredControls' className='flexContainer'>
                    <SkipPreviousView {...this.state} />
                    <PlayPauseView {...this.state} />
                    <SkipNextView {...this.state} />
                </div>

                <ToggleScreenView {...this.state} />
            </div>
        )
    }

    /*************************************************
    *
    *                   help functions
    *
    *************************************************/

}

export default Container.create(MusicControlContainer)



