'use strict'

// import area
import React         from 'react'

import { withTheme } from '@material-ui/core/styles'

import Typography    from '@material-ui/core/Typography'
import Button        from '@material-ui/core/Button'

import IconButton    from '@material-ui/core/IconButton'
import Icon          from '@material-ui/core/Icon'

import PlayArrow     from '@material-ui/icons/PlayArrow'
import Pause         from '@material-ui/icons/Pause'




// variables area

class PlayPauseView extends React.Component {
    constructor(props) {
        super(props)

        let palette = props.theme.palette
        let type    = palette.type

        this.className = this.constructor.name
        this.state     = {}

        this.style = {
            root: {
                // display: 'flex'
            },
            icon: {
                fontSize: 48
            }
        }

        this.buttonHandler = this.buttonHandler.bind(this)
    }

    /*
    *   create the controls and subscribe to the values
    */
    componentWillMount() {

    }

    /*
    *   remove the listeners here
    */
    componentWillUnmount() {

    }

    render() {
        let playerState = this.props.musicControlStore.get('playerState')
        this.isPlaying  = playerState.isPlaying

        return (
            <div id='playPauseView' style={this.style.root}>
                <IconButton id='play' aria-label="play button" onClick={this.buttonHandler}>

                    {
                        !this.isPlaying &&
                        <PlayArrow style={this.style.icon}  />
                    }

                    {
                        this.isPlaying &&
                        <Pause style={this.style.icon}  />
                    }

                </IconButton>
            </div>
        )
    }

    buttonHandler(event) {
        this.props.onKeyPressed('isPlaying', !this.isPlaying)
    }

    /**********************************************
    *
    *                  help functions
    *
    **********************************************/


}

export default withTheme()(PlayPauseView)



