'use strict'

// import area
import React         from 'react'
import { withTheme } from '@material-ui/core/styles'

import Typography    from '@material-ui/core/Typography'

import Icon          from '@material-ui/core/Icon'
import Button        from '@material-ui/core/Button'
import IconButton    from '@material-ui/core/IconButton'

import SkipPrevious  from '@material-ui/icons/SkipPrevious'

// variables area

class SkipPreviousView extends React.Component {
    constructor(props) {
        super(props)

        this.className = this.constructor.name
        this.state     = {}
        this.style = {
            root: {
                // display: 'flex'
            },
        }

        this.buttonHandler = this.buttonHandler.bind(this)
    }

    /*
    *   create the controls and subscribe to the values
    */
    componentWillMount() {

    }

    /*
    *   remove the listeners here
    */
    componentWillUnmount() {

    }

    // IDEA: use the 'component' attribute to parse the buttons as 'span'
    // than you can align them with the baseline option
    render() {
        // TODO: get the current state to decide which button shall be displayed

        return (
            <div id='skipPreviousView' style={this.style.root}>
                <IconButton id='skipPrevious' aria-label="play button" onClick={this.buttonHandler}>
                    <SkipPrevious style={{ fontSize: 48 }}  />
                </IconButton>
            </div>
        )
    }

    buttonHandler(event) {
        console.log(`[${this.className}] buttonHandler '${event.currentTarget.id}' called`)
    }

    /**********************************************
    *
    *                  help functions
    *
    **********************************************/


}

export default withTheme()(SkipPreviousView)



