'use strict'

// import area
import React         from 'react'
import { withTheme } from '@material-ui/core/styles'

import Typography    from '@material-ui/core/Typography'
import Button        from '@material-ui/core/Button'

import IconButton    from '@material-ui/core/IconButton'
import Icon          from '@material-ui/core/Icon'

import Binding       from './binding'

// variables area

class BindingsView extends React.Component {
    constructor(props) {
        super(props)

        this.className = this.constructor.name
        this.state     = {}
        let palette    = props.theme.palette
        let type       = palette.type


        this.style = {
            root: {
                // flexGrow  : 1,
                marginTop : '5%',
            },
            icon: {
                fontSize: 48
            }
        }

        this.buttonHandler = this.buttonHandler.bind(this)
        this.parseBindings = this.parseBindings.bind(this)
    }

    /*
    *   create the controls and subscribe to the values
    */
    componentWillMount() {

    }

    /*
    *   remove the listeners here
    */
    componentWillUnmount() {

    }

    render() {
        // let playerState = this.props.musicControlStore.get('playerState')
        // this.isPlaying  = playerState.isPlaying
        let searchStore    = this.props.searchStore
        let bindings       = searchStore.get('bindings')
        let parsedBindings = this.parseBindings(bindings)

        return (
            <div id='bindingsView' className='flexContainer' style={this.style.root}>
                {
                    parsedBindings.length === 0 &&
                    <Typography variant='display1' align='center'>
                        No bindings available
                    </Typography>
                }

                {
                    parsedBindings.length >= 0 &&
                    parsedBindings
                }

            </div>
        )
    }

    buttonHandler(event) {
        this.props.onKeyPressed('isPlaying', !this.isPlaying)
    }

    /**********************************************
    *
    *                  help functions
    *
    **********************************************/

    parseBindings(list) {
        let parsedBindings = []

        for(let [index, entry] of list.entries()) {
            let bindingObject = (
                <Binding key={index} {...entry} {...this.props} />
            )

            parsedBindings.push(bindingObject)
        }

        return parsedBindings
    }
}

export default withTheme()(BindingsView)



