'use strict'

import SearchActionTypes from './searchActionTypes'
import AppDispatcher     from './../appDispatcher'

const Actions = {
    inputChanged(newValue){
        AppDispatcher.dispatch({
            type: SearchActionTypes.INPUT_CHANGED,
            newValue
        })
    },

    selectionChanged(value, isActive) {
        AppDispatcher.dispatch({
            type: SearchActionTypes.SELECTION_CHANGED,
            value,
            isActive
        })
    },

    search() {
        AppDispatcher.dispatch({
            type: SearchActionTypes.SEARCH
        })
    },

    subscribeTo(path, event, storePath) {
        AppDispatcher.dispatch({
            type: SearchActionTypes.SUBSCRIBE_TO,
            path,
            event,
            storePath
        })
    },

    subscribeResponse(name, value) {
        AppDispatcher.dispatch({
            type: SearchActionTypes.SUBSCRIBE_RESPONSE,
            name,
            value
        })
    },

    resultSelection(object) {
        AppDispatcher.dispatch({
            type: SearchActionTypes.RESULT_SELECTION,
            object
        })
    },

    selectionAccepted() {
        AppDispatcher.dispatch({
            type: SearchActionTypes.SELECTION_ACCEPTED,
        })
    },

    selectionCanceled() {
        AppDispatcher.dispatch({
            type: SearchActionTypes.SELECTION_CANCELED,
        })
    }
}

export default Actions



