'use strict'

// import area
import Immutable               from 'immutable'

import AbstractStore           from './AbstractStore'
import AppActionTypes          from './../actions/appActionTypes'

import MusicControlActionTypes from './../actions/musicControlActionTypes'
import AppDispatcher           from './../appDispatcher'
import Utils                   from './../utils/utils'


// import MusicControlDataManager from './../dataManagers/musicControlDataManager'

// variables area

class MusicControlStore extends AbstractStore {
    constructor() {
       super(AppDispatcher)

       this.className = this.constructor.name
       // this.musicControlDataManager = new MusicControlDataManager()
    }

    getInitialState() {
        let initialState = Immutable.Map({
            playerState: {
                isPlaying : false,
                volume    : 0,
                song      : {
                    title        : undefined,
                    interpreter  : undefined,
                    selectedFrom : undefined
                }
            },
        })

       return initialState
    }

    reduce(state, action) {
        switch (action.type) {
            case MusicControlActionTypes.KEY_PRESSED: {
                console.log(`[${this.className}] ${action.type}`)
                console.log(action)
                console.log(state)

                return state.setIn(['playerState', action.id], action.value)
                // return state
            }

            case MusicControlActionTypes.KEY_PRESSED_RESPONSE: {
                console.log(`[${this.className}] ${action.type}`)
                console.log(action)

                // let newId      = Utils.getUID()
                // let loadObject = this.appDataManager.fetchData(newId, action.key, action.path, action.data)

                // return state.setIn(['files', newId], loadObject)
                return state
            }

            case AppActionTypes.REGISTER_AT_SERVER_RESPONSE: {
                console.log(`[${this.className}] ${action.type}`)
                console.log(action)
                // let response = action.response


                // return state.setIn(['playerState'], response.displayState)
                return state
            }

            default:
               // console.log(`[${this.className}] unknown type`)
               return state
       }
    }

    /*************************************************
    *
    *                   help functions
    *
    *************************************************/

}

export default new MusicControlStore()



