'use strict'

// import area
import React               from 'react'
import { withTheme }       from '@material-ui/core/styles'

import ListItem            from '@material-ui/core/ListItem'
import ListItemAvatar      from '@material-ui/core/ListItemAvatar'
import ListItemText        from '@material-ui/core/ListItemText'
import Avatar              from '@material-ui/core/Avatar'
import Typography          from '@material-ui/core/Typography'


// variables area

class ResultItem extends React.Component {
    constructor(props) {
        super(props)

        let theme      = props.theme
        this.className = this.constructor.name
        this.state     = {
            uri           : props.track.uri,
            icon          : props.track.icon,
            artists       : props.track.artists,
            trackName     : props.track.trackName,
            parsedArtists : '',
            isSelected    : false,
        }

        this.style         = {
            root: {
                // width: '60%'
            },
            selected: {
                backgroundColor: theme.palette.secondary.light
            },
            selectedPrimaryText: {
                color: theme.palette.getContrastText(theme.palette.secondary.light)
            },
            selectedSecondaryText: {
                color: theme.palette.getContrastText(theme.palette.secondary.light)
            }
        }

        this.onClick = this.onClick.bind(this)
    }

    componentDidMount() {}

    render() {
        const searchStore = this.props.searchStore
        let artists       = this.parseArtists(this.state.artists)
        let rootStyle     = Object.assign({}, this.style.root)
        let primaryText   = {}
        let secondaryText = {}

        return (
            <ListItem button onClick={this.onClick} style={rootStyle} >
                <ListItemAvatar>
                    <Avatar src={this.state.icon} />
                </ListItemAvatar>
                <ListItemText
                      primary   = { this.state.trackName }
                      primaryTypographyProps = { primaryText }
                      secondary = { artists }
                      secondaryTypographyProps = { secondaryText }
                />
            </ListItem>
        )
    }

    onClick(event) {
        console.log(`[${this.state.trackName}] selected`)

        // let isSelected = this.state.isSelected
        // let newState   = !isSelected

        this.props.resultSelection(this.props.track)
        // this.setState({isSelected: newState})
    }

    /*************************************************
    *
    *                   help functions
    *
    *************************************************/

    parseArtists(list) {
        let parsedArtists = ''
        let numberOfArtists = list.length

        for(let [index, entry] of list.entries()) {
            parsedArtists += entry.name

            if(index != numberOfArtists - 1)
                parsedArtists += ', '
        }

        return parsedArtists
    }
}

export default withTheme()(ResultItem)



