'use strict'

// import area
import React               from 'react'
import { withTheme }       from '@material-ui/core/styles'

import Button from '@material-ui/core/Button';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import Typography          from '@material-ui/core/Typography'


// variables area

class Confirmation extends React.Component {
    constructor(props) {
        super(props)

        let theme      = props.theme
        this.className = this.constructor.name
        this.state     = {}
        this.style     = {
            root: {
                width: '74%'
            },
        }

        this.onAccept     = this.onAccept.bind(this)
        this.onDecline    = this.onDecline.bind(this)
        this.parseArtists = this.parseArtists.bind(this)
    }

    componentDidMount() {
        console.log(`[${this.state.trackName}] mounted`)
        console.log(this.props)
    }

    render() {
        const searchStore      = this.props.searchStore
        const showConfirmation = searchStore.get('showConfirmation')
        const selectedTrack    = searchStore.get('selectedResult')
        const paperstyle       = this.style.root

        if(!selectedTrack)
            return null

        return (
            <Dialog
                open             = { showConfirmation }
                onClose          = { this.onDecline }
                aria-labelledby  = "alert-dialog-title"
                aria-describedby = "alert-dialog-description"
                PaperProps       = { paperstyle }
            >
                <DialogTitle id="alert-dialog-title">
                    {'Add this track?'}
                </DialogTitle>
                <DialogContent>
                    <DialogContentText color='textSecondary'>
                        { selectedTrack.trackName }  <br />
                        { this.parseArtists(selectedTrack.artists) }
                    </DialogContentText>
                </DialogContent>
                <DialogActions>
                    <Button onClick={this.onDecline} color="secondary">
                        No
                    </Button>
                    <Button onClick={this.onAccept} color="secondary" autoFocus>
                        Yes
                    </Button>
                </DialogActions>
            </Dialog>
        )
    }

    onAccept(event) {
        console.log(`[${this.className}] onAccept`)
        this.props.acceptSelection()
    }

    onDecline(event) {
        console.log(`[${this.className}] onDecline`)
        this.props.cancelSelection()
    }

    /*************************************************
    *
    *                   help functions
    *
    *************************************************/

    parseArtists(list) {
        let parsedArtists = ''
        let numberOfArtists = list.length

        for(let [index, entry] of list.entries()) {
            parsedArtists += entry.name

            if(index != numberOfArtists - 1)
                parsedArtists += ', '
        }

        return parsedArtists
    }
}

export default withTheme()(Confirmation)



