'use strict'

import MusicControlActionTypes from './musicControlActionTypes'
import AppDispatcher           from './../appDispatcher'

const Actions = {
    keyPressed(id, value) {
        AppDispatcher.dispatch({
            type: MusicControlActionTypes.KEY_PRESSED,
            id,
            value
        })
    },
    keyPressedResponse(id, response) {
        AppDispatcher.dispatch({
            type: MusicControlActionTypes.KEY_PRESSED_RESPONSE,
            id,
            response
        })
    },
}

export default Actions



