'use strict'

// import area
import React               from 'react'
import { withTheme }       from '@material-ui/core/styles'

import TextField           from '@material-ui/core/TextField'
import Typography          from '@material-ui/core/Typography'

import IconButton          from '@material-ui/core/IconButton'
import Close               from '@material-ui/icons/Close'

// variables area

class SearchBar extends React.Component {
    constructor(props) {
        super(props)

        let theme      = props.theme
        this.className = this.constructor.name
        this.state     = {}
        this.style     = {
            root: {
                alignItems    : 'flex-end',
                paddingTop    : '5%',
                paddingBottom : '2%',
            },
            searchField: {
                width: '80%',
            },
            closeIcon: {
                fontSize   : 32,
                marginLeft : '2%',
                alignSelf  : 'flex-end',
            },
            closeDummy: {
                fontSize    : 32,
                marginRight : '2%',
                alignSelf   : 'flex-end',
                visibility  : 'hidden'
            },
        }

        this.onInputChange = this.onInputChange.bind(this)
        this.searchHandler = this.searchHandler.bind(this)
    }

    render() {
        const classes          = this.props.classes
        const searchStore      = this.props.searchStore
        const searchFieldValue = searchStore.get('input')
        const searchResults    = undefined

        return (
            <div id='searchBar' className='flexContainer' style={this.style.root}>
                <TextField
                    id         = 'searchField'
                    label      = 'Search...'
                    type       = 'search'
                    value      = { searchFieldValue }
                    style      = { this.style.searchField }
                    onChange   = { this.onInputChange }
                    onKeyDown  = { this.searchHandler }
                />
            </div>
        )
    }

    searchHandler(event) {
        let target           = event.target.id
        let sendNotification = false

        if(target === 'searchField' && event.key === 'Enter')
            sendNotification = true

        if(sendNotification)
            this.props.search()
    }

    onInputChange(event) {
        let newValue = event.target.value

        this.props.inputChanged(newValue)

        // if(newValue === '')
            // this.props.onSearchTriggered()
    }

    /*************************************************
    *
    *                   help functions
    *
    *************************************************/
}

export default withTheme()(SearchBar)



