'use strict'

// import area
import React         from 'react'
import { withTheme } from '@material-ui/core/styles'

import Avatar        from '@material-ui/core/Avatar'

// variables area
const ACTIVE   = 1.0
const INACTIVE = 0.4

class Binding extends React.Component {
    constructor(props) {
        super(props)

        this.className = this.constructor.name
        this.state     = {
            name    : props.name,
            icon    : props.icon,
            opacity : ACTIVE
        }

        let palette    = props.theme.palette
        let type       = palette.type


        this.style = {
            root: {
                // flexGrow  : 1,
                marginLeft : '3%',
                marginRight: '3%',
            },
            icon: {
                fontSize: 48
            }
        }

        this.onClick = this.onClick.bind(this)
    }

    /*
    *   create the controls and subscribe to the values
    */
    componentWillMount() {
    }

    /*
    *   remove the listeners here
    */
    componentWillUnmount() {

    }

    render() {
        // let playerState = this.props.musicControlStore.get('playerState')
        // this.isPlaying  = playerState.isPlaying
        let searchStore = this.props.searchStore
        let uri         = 'http://localhost:8080/' + this.state.icon
        let style       = Object.assign({}, this.style.root)

        style.opacity = this.state.opacity

        return (
            <Avatar alt={this.state.name} src={uri} style={style} onClick={this.onClick} />
        )
    }

    onClick(event) {
        let isActive   = true
        let newOpacity = ACTIVE

        if(this.state.opacity === ACTIVE) {
            newOpacity = INACTIVE
            isActive   = false
        }

        this.setState({ opacity: newOpacity })
        this.props.selectionChanged(this.state.name, isActive)
    }

    /**********************************************
    *
    *                  help functions
    *
    **********************************************/

}

export default withTheme()(Binding)



