'use strict'

// import area
import Immutable         from 'immutable'

import AbstractStore     from './AbstractStore'
import AppDispatcher     from './../appDispatcher'
import Utils             from './../utils/utils'
import LoadObject        from './../utils/loadObject/loadObject'
import LoadObjectState   from './../utils/loadObject/loadObjectState'

import AppActionTypes    from './../actions/appActionTypes'
import SearchActionTypes from './../actions/searchActionTypes'
import SearchDataManager from './../dataManagers/searchDataManager'

// variables area

class SearchStore extends AbstractStore {
    constructor() {
       super(AppDispatcher)

       this.className           = this.constructor.name
       this.searchDataManager   = new SearchDataManager()

       this.subscribeTo         = this.subscribeTo.bind(this)
       this.addSelection        = this.addSelection.bind(this)
       this.removeSelection     = this.removeSelection.bind(this)
       this.setDefaultSelection = this.setDefaultSelection.bind(this)
    }

    getInitialState() {
        let initialState = Immutable.Map({
            buffer           : Immutable.Map(),
            subscriptions    : Immutable.Map(),
            files            : Immutable.Map(),
            input            : '',
            bindings         : [],
            selectedBindings : [],
            searchResults    : [],
            selectedResult   : undefined,
            showConfirmation : false,
        })

       return initialState
    }

    reduce(state, action) {
        switch (action.type) {
            case AppActionTypes.REGISTER_AT_SERVER: {
                console.log(`[${this.className}] ${action.type}`)
                console.log(action)

                let loadObject = new LoadObject(action.id, action.key, undefined, undefined, undefined, action.data)


                return state.setIn([action.key], loadObject)
            }

            case AppActionTypes.REGISTER_AT_SERVER_RESPONSE: {
                // console.log(`[${this.className}] ${action.type}`)
                // console.log(action)

                let response         = action.loadObject
                let fetchKey         = response.key
                let clearedBuffer    = undefined
                let selectedBindings = this.setDefaultSelection(response.value.bindings)

                state         = state.setIn(['selectedBindings'], selectedBindings)
                state         = state.setIn(['bindings'], response.value.bindings)
                state         = state.setIn([response.id], response)

                clearedBuffer = this.clearBuffer(state, fetchKey)

                return state.setIn(['buffer', fetchKey], clearedBuffer)
            }

            case SearchActionTypes.INPUT_CHANGED: {
                // console.log(`[${this.className}] ${action.type}`)
                // console.log(action)

                if(action.newValue == '') {
                    state = state.setIn(['searchResults'], [])
                    state = state.setIn(['selectedResult'], undefined)
                }

                return state.setIn(['input'], action.newValue)
            }

            case SearchActionTypes.SELECTION_CHANGED: {
                // console.log(`[${this.className}] ${action.type}`)
                // console.log(action)

                let newList = Array.from(state.get('selectedBindings'))

                if(action.isActive)
                    newList = this.addSelection(newList, action.value)
                else
                    newList = this.removeSelection(newList, action.value)

                return state.setIn(['selectedBindings'], newList)
            }

            case SearchActionTypes.SEARCH: {
                // console.log(`[${this.className}] ${action.type}`)
                // console.log(action)
                // console.log(state.get('input'))

                let selectedBindings = state.get('bindings')
                let query            = state.get('input')
                let isBindingsEmpty  = state.get('selectedBindings').length == 0
                let isQueryEmpty     = query === ''

                // this.searchDataManager.search(selectedBindings, query)

                if(!isBindingsEmpty && !isQueryEmpty)
                    this.searchDataManager.search(selectedBindings, query)
                else
                    console.warn('no bindings selected or query is empty')

                return state
            }

            case SearchActionTypes.SUBSCRIBE_TO: {
                // console.log(`[${this.className}] ${action.type}`)
                // console.log(action)

                // this.searchDataManager.subscribeTo(action)
                // (['registration'], SEARCH_RESPONSE, searchResponse)

                let isFilePending  = this.isPending(action.path)
                let func           = this.subscribeTo
                let updatedState   = state.setIn(['subscriptions', action.event], undefined)

                if(isFilePending) {
                    let bufferName   = action.path[0]
                    let bufferedList = this.addToBuffer(action, func)

                    return updatedState.setIn(['buffer', bufferName], bufferedList)
                }
                else {
                    func(state, action)

                    return updatedState
                }
            }

            case SearchActionTypes.SUBSCRIBE_RESPONSE: {
                console.log(`[${this.className}] ${action.type}`)
                console.log(action)

                return state.setIn(action.name, action.value)
            }

            case SearchActionTypes.RESULT_SELECTION: {
                console.log(`[${this.className}] ${action.type}`)
                console.log(action)

                state = state.setIn(['selectedResult'], action.object)

                return state.setIn(['showConfirmation'], true)
            }

            case SearchActionTypes.SELECTION_ACCEPTED: {
                console.log(`[${this.className}] ${action.type}`)
                console.log(action)

                state = state.setIn(['showConfirmation'], false)

                return state
            }

            case SearchActionTypes.SELECTION_CANCELED: {
                console.log(`[${this.className}] ${action.type}`)
                console.log(action)
                state = state.setIn(['showConfirmation'], false)

                return state.setIn(['selectedResult'], undefined)
            }

            default:
               // console.log(`[${this.className}] unknown type`)
               return state
       }
    }

    /*************************************************
    *
    *                   help functions
    *
    *************************************************/

    subscribeTo(state, action) {
        // console.log(`[${this.className}] subscribeTo ${action.event}`)
        // console.log(action)

        this.searchDataManager.subscribeTo(action.event, action.storePath)
    }

    addSelection(list, value) {
        let newList  = Array.from(list)
        let hasEntry = newList.find(x => x === value)

        if(!hasEntry)
            newList.push(value)

        return newList
    }

    removeSelection(list, value) {
        let newList  = Array.from(list)
        let hasEntry = newList.indexOf(value)

        if(hasEntry >= 0)
            newList = newList.slice(hasEntry, 0)

        return newList
    }

    setDefaultSelection(list) {
        let defaultSelection = []

        for(let entry of list)
            defaultSelection.push(entry.name)

        return defaultSelection
    }

    updateResultSelection(source, name, isSelected) {
        let updatedList = Array.from(source)



        return updatedList
    }
}

export default new SearchStore()



