'use strict'

// import area
import React               from 'react'
import { withTheme }       from '@material-ui/core/styles'

import List                from '@material-ui/core/List'

import ResultItem          from './resultItem'

// variables area

class SearchResults extends React.Component {
    constructor(props) {
        super(props)

        let theme      = props.theme
        this.className = this.constructor.name
        this.state     = {}
        this.style     = {
            root: {
                flexGrow      : 1,
                flexDirection : 'row',
                marginTop     : '5%',
            },
            list: {
                maxHeight     : 300,
                overflow      : 'auto'
            }
        }

    }

    componentDidMount() {}

    render() {
        const searchStore   = this.props.searchStore
        const searchResults = searchStore.get('searchResults')

        if(searchResults.length === 0)
            return null

        let results = this.createResultList(searchResults)

        return (
            <div id='searchResults' className='flexContainer' style={this.style.root} >
                <List dense style={this.style.list}>
                    { results }
                </List>
            </div>
        )
    }

    createResultList(list) {
        // let shortList = list.slice(0,5)
        let shortList = list
        let results = []


        for(let [index, entry] of shortList.entries()) {
            let newItem = (
                <ResultItem key={index} track={entry} {...this.props} />
            )

            results.push(newItem)
        }

        return results
    }

    /*************************************************
    *
    *                   help functions
    *
    *************************************************/

}

export default withTheme()(SearchResults)



