'use strict'

// import area
import React               from 'react'
import { Container }       from 'flux/utils'

// flux stuff
import AppStore            from './../data/stores/appStore'
import AppActions          from './../data/actions/appActions'

import SearchStore         from './../data/stores/searchStore'
import SearchActions       from './../data/actions/searchActions'

import Utils               from './../data/utils/utils'
import WebSocketEvents     from './../data/utils/websocket/websocketEvents'

// views/containers
import BindingsView        from './../views/bindings/bindingsView'
import SearchBar           from './../views/search/searchBar'
import SearchResults       from './../views/search/searchResults'
import Confirmation        from './../views/search/confirmation'

// variables area

class SearchContainer extends React.Component {
    constructor() {
        super()

        this.className = this.constructor.name
        this.state     = {}
        this.style     = {
            root: {
                flexGrow       : 1,
                marginBottom   : '2%',
                flexDirection  : 'column',
                justifyContent : 'flex-start'
            }
        }
    }

    /*
    *
    */
    componentDidMount() {

        // AppActions.registerAtServer(registration)

        // TODO: register listener for search response
        let path      = ['registration']
        let event     = WebSocketEvents.SEARCH_RESPONSE
        let storePath = ['searchResults']

        SearchActions.subscribeTo(path, event, storePath)
    }

    /*
    *   loads all necessary stores
    *   NOTE: this is a required function for a container
    */
    static getStores() {
        let stores = [
            AppStore,
            SearchStore
        ]

        return stores
    }

    /*
    *   load your state here
    *   NOTE: this is a required function for a container
    */
    static calculateState(prevState) {
        let state = {
            appStore         : AppStore.getState(),
            searchStore      : SearchStore.getState(),

            inputChanged     : SearchActions.inputChanged,
            selectionChanged : SearchActions.selectionChanged,
            search           : SearchActions.search,
            resultSelection  : SearchActions.resultSelection,
            acceptSelection  : SearchActions.selectionAccepted,
            cancelSelection  : SearchActions.selectionCanceled,
        }

        return state
    }

    render() {
        let appStore = this.state.appStore
        let theme    = appStore.get('theme').object

        return (
            <div id='searchContent' className='flexContainer' style={this.style.root}>
                <BindingsView {...this.state} />
                <SearchBar {...this.state} />
                <SearchResults {...this.state} />
                <Confirmation {...this.state} />
            </div>
        )
    }

    /*************************************************
    *
    *                   help functions
    *
    *************************************************/

}

export default Container.create(SearchContainer)



